.Header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  padding: 1px 30px;
  box-shadow: 0px 3px 7px 10px gray;
  border-radius: 0px 0px 10px 10px;
  background-image: linear-gradient(to bottom right, purple, red);
  max-height: 15vh;
  overflow: hidden;
  -webkit-align-items: center;
          align-items: center; }

.Header-col-1 {
  grid-column: 1;
  justify-self: start; }

.Header-col-2 {
  grid-column: 2;
  justify-self: center;
  text-align: center;
  color: white;
  font-family: 'Dancing Script', cursive;
  font-size: 30px;
  margin: 0; }

@media screen and (max-width: 600px) {
  .Header-col-2 {
    font-size: 20px; } }

.Header-col-3 {
  grid-column: 3;
  justify-self: end; }

a {
  font-weight: bold;
  text-decoration: none; }

.MenuNavigator {
  display: grid;
  -webkit-align-items: center;
          align-items: center; }

.MenuNavigator-item {
  color: white; }

.Modal {
  position: fixed;
  top: 20vh;
  bottom: 20vh;
  left: 20vw;
  right: 20vw;
  background-color: white;
  display: grid;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  vertical-align: top;
  padding: 10px 30px;
  box-shadow: 0px 3px 7px 10px gray;
  border-radius: 0px 0px 10px 10px;
  background-image: linear-gradient(to bottom right, purple, red);
  color: white; }

.Modal-close {
  color: white;
  background-color: transparent;
  font-weight: bold;
  border-radius: 100px;
  border-radius: 5px;
  padding: 5px;
  -webkit-align-self: end;
          align-self: end; }

.Content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 50px;
  grid-column-gap: 25px;
  -webkit-column-gap: 25px;
          column-gap: 25px;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center; }

.Content-block {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center; }

.Content img {
  width: 300px;
  height: auto;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  z-index: 1; }

.Content img:hover {
  -webkit-animation: artwork-motion 1s linear normal forwards;
          animation: artwork-motion 1s linear normal forwards; }

@-webkit-keyframes artwork-motion {
  from {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg); }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

@keyframes artwork-motion {
  from {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg); }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

